  /* Стили для мобильного слайдера */
  .mobile-slider {
    display: none;
  }
  
  .desktop-only {
    display: block;
  }
  
  @media (max-width: 768px) {
    .mobile-slider {
      display: block;
      position: relative;
      width: 100%;
      overflow: hidden;
      margin-bottom: 15px;
      order: 4; /* Перемещаем слайдер вниз */
    }
    
    .desktop-only {
      display: none;
    }
    
    .mobile-slider__container {
      display: flex;
      transition: transform 0.3s ease;
      width: 100%;
    }
    
    .mobile-slider__slide {
      flex: 0 0 100%;
      width: 100%;
    }
    
    .mobile-slider__dots {
      display: flex;
      justify-content: center;
      margin-top: 10px;
    }
    
    .mobile-slider__dot {
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background-color: #ccc;
      margin: 0 5px;
      cursor: pointer;
    }
    
    .mobile-slider__dot.active {
      background-color: #00b67a;
    }
    
    /* Изменяем порядок элементов в мобильной версии */
    .products__grid {
      display: flex;
      flex-direction: column;
    }
    
    .product-card.div1 {
      order: 1;
    }
    
    .product-card.div4 {
      order: 2;
    }
    
    .product-card.div5 {
      order: 3;
    }
    
    /* Стили для карточек в мобильной версии */
    .product-card {
      width: 100%;
      margin-bottom: 10px;
    }
    
    .product-card__image {
      min-height: 180px;
    }
  }
  @media (max-width: 480px) {
    .product-card__image {
      min-height: 160px;
    }
  }
  
